.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  text-align: center;
  background-color: #282c34;
}

.App-header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 10vh;
  color: rgb(255,212,3) ;
}

.App-footer {
  display: flex;
  align-items: center;
  height: 10vh;
  color: rgb(255,212,3);
}

.storageContent {
  padding-top: 118px;
}

.sidebar{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 20px;
}

.content{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 90%;
  padding: 10px;
}

.main{
  display: flex;
  min-height: 80vh;
}

.fotoStorage{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  border-radius: 10px;
  background-color: #20201f;
}

.fotoSearch {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  border-radius: 10px;
  background-color: #494a4d;
}

.imgItem{
  height: 300px;
  width: 300px;
  padding: 10px;
  overflow: hidden;
  border-radius: 20px;
}

.imgList{
  display: flex;
  flex-direction: column;
  justify-content:flex-end;
  align-items: center;
  height: 400px;
  width: 330px;
  margin: 10px;
  overflow: hidden;
  border-radius: 10px;
  background-color: #282c34;
}

.titleImg {
  height: 70px;
  overflow: hidden;
  font-size: 21px;
  color: rgb(255,212,3);
}

.tools {
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.pageNumber {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-size: 27px;
  color: rgb(255,212,3);
}

.tools {
  margin: 20px;
}

.buttonTools {
  height: 35px;
  margin: 10px;
}

.button, .buttonTools{
  display: inline-block;
  font-size: 125%;
  font-weight: 700;
  text-decoration: none;
  user-select: none;
  padding: .25em .5em;
  outline: none;
  cursor: pointer;
  color: black;
  border: 1px solid rgb(250,172,17);
  border-radius: 7px;
  background: rgb(255,212,3) linear-gradient(rgb(255,212,3), rgb(248,157,23));
  box-shadow: inset 0 -2px 1px rgba(0,0,0,0), inset 0 1px 2px rgba(0,0,0,0), inset 0 0 0 60px rgba(255,255,0,0);
  transition: box-shadow .2s, border-color .2s;
} 
.button:hover, .buttonTools:hover {
  box-shadow: inset 0 -1px 1px rgba(0,0,0,0), inset 0 1px 2px rgba(0,0,0,0), inset 0 0 0 60px rgba(255,255,0,.5);
}
.button:active, .buttonTools:active {
  padding: calc(.25em + 1px) .5em calc(.25em - 1px);
  border-color: rgba(177,159,0,1);
  box-shadow: inset 0 -1px 1px rgba(0,0,0,.1), inset 0 1px 2px rgba(0,0,0,.3), inset 0 0 0 60px rgba(255,255,0,.45);
}